import { Alert, AlertTitle, Typography } from '@mui/material';
import React from 'react';

const FileValidation = (props) => {
  const { verified, component, validationResult } = props;
  if (verified) {
    return (
      <div>
        <Typography variant="h5" mb={2}>
          File Validation
        </Typography>

        <Alert severity="success">File validation succeeded!</Alert>
      </div>
    );
  }
  if (component === 'indicator') {
    return (
      <div>
        <Typography variant="h5" mb={2}>
          File Validation
        </Typography>

        {Array.from(validationResult.errors).map((validation, index) => (
          <Alert severity="error" key={index} sx={{ mb: '10px' }} >
            {validation}
          </Alert>
        ))}
      </div>
    );
  } else {
    if (validationResult.errors) {
      return (
        <div>
          <Typography variant="h5" mb={2}>
            File Validation
          </Typography>
          {Object.entries(validationResult.errors).map(([ key, value]) => (
            value.length > 0 && (
              <Alert severity="error" key={key} sx={{ mb: '10px' }} >
                <AlertTitle>{key} errors in:</AlertTitle>
                {value.join(', ')}
              </Alert>
            )
          ))}
        </div>
      )
    }
  }
};

export { FileValidation };
